/* Quote sections */
.quote-bg {
  background-color: #3d405b;
}

/* Founder's quote styling */
.founder-quote {
  border-left: 4px solid #81b29a;
}

/* Gallery item hover effect */
.gallery-item {
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.gallery-item:hover {
  transform: scale(1.03);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

/* Event card hover effect */
.event-card {
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.event-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.08);
}

/* Responsive utilities */
@media (max-width: 768px) {
  h1 {
    font-size: 2.5rem;
  }
  
  h2 {
    font-size: 2rem;
  }
}


 :root {
    --ciks-crimson:   #7c1d1d;
    --ciks-crimson2:  #5a1010;
    --ciks-gold:      #c9a84c;
    --ciks-gold-lt:   #e8d49a;
    --ciks-cream:     #fdf8f0;
    --ciks-dark:      #180a04;
    --ciks-ink:       #1a1008;
    --ciks-muted:     #7a6155;
  }

  /* ══════════════════════════════════════
     THREE FOUNDATIONS SECTION
  ══════════════════════════════════════ */
  .foundations-section {
    background: linear-gradient(175deg, #120505 0%, #2a0c0c 40%, #1a0806 100%);
    position: relative;
    padding: 7rem 0 8rem;
    overflow: hidden;
  }

  /* Background geometric mandala */
  .foundations-section::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 900px;
    height: 900px;
    background: radial-gradient(circle, rgba(201,168,76,.07) 0%, transparent 65%);
    pointer-events: none;
  }

  /* Subtle grid texture */
  .foundations-section::after {
    content: '';
    position: absolute;
    inset: 0;
    background-image:
      linear-gradient(rgba(201,168,76,.04) 1px, transparent 1px),
      linear-gradient(90deg, rgba(201,168,76,.04) 1px, transparent 1px);
    background-size: 60px 60px;
    pointer-events: none;
  }

  /* ── Section Header ── */
  .foundations-eyebrow {
    font-family: 'Cinzel', serif;
    font-size: .68rem;
    letter-spacing: .42em;
    text-transform: uppercase;
    color: var(--ciks-gold);
    opacity: .8;
  }

  .foundations-title {
    font-family: 'Cormorant Garamond', serif;
    font-size: clamp(2.4rem, 5vw, 3.8rem);
    font-weight: 300;
    color: #fff;
    line-height: 1.1;
    letter-spacing: -.01em;
  }

  .foundations-title em {
    font-style: italic;
    color: var(--ciks-gold-lt);
  }

  .gold-rule {
    display: block;
    width: 50px;
    height: 2px;
    background: linear-gradient(90deg, var(--ciks-crimson), var(--ciks-gold));
    margin: 1.2rem auto;
  }

  /* ── Card Grid ── */
  .foundations-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
    position: relative;
    z-index: 1;
  }

  @media (max-width: 900px) {
    .foundations-grid {
      grid-template-columns: 1fr;
      max-width: 460px;
      margin-left: auto;
      margin-right: auto;
    }
  }

  /* ── Pillar Card ── */
  .pillar-card {
    position: relative;
    background: rgba(255,255,255,.035);
    border: 1px solid rgba(201,168,76,.18);
    border-radius: 2px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    transition: transform .5s cubic-bezier(.22,1,.36,1),
                border-color .4s ease,
                background .4s ease;
    cursor: default;
  }

  .pillar-card:hover {
    transform: translateY(-10px);
    border-color: rgba(201,168,76,.55);
    background: rgba(255,255,255,.06);
  }

  /* Top gold line that expands */
  .pillar-card::before {
    content: '';
    position: absolute;
    top: 0; left: 0; right: 0;
    height: 3px;
    background: linear-gradient(90deg, var(--ciks-crimson), var(--ciks-gold), var(--ciks-crimson));
    transform: scaleX(0);
    transform-origin: center;
    transition: transform .5s cubic-bezier(.22,1,.36,1);
  }

  .pillar-card:hover::before {
    transform: scaleX(1);
  }

  /* Large ghost numeral */
  .pillar-numeral {
    position: absolute;
    top: -1.5rem;
    right: 1rem;
    font-family: 'Cinzel', serif;
    font-size: 9rem;
    font-weight: 700;
    color: rgba(201,168,76,.06);
    line-height: 1;
    user-select: none;
    pointer-events: none;
    transition: color .4s ease;
  }

  .pillar-card:hover .pillar-numeral {
    color: rgba(201,168,76,.12);
  }

  /* Image strip with overlay */
  .pillar-img-wrap {
    position: relative;
    height: 200px;
    overflow: hidden;
    flex-shrink: 0;
  }

  .pillar-img-wrap img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    filter: saturate(.6) brightness(.5) sepia(.3);
    transition: filter .6s ease, transform .6s cubic-bezier(.22,1,.36,1);
  }

  .pillar-card:hover .pillar-img-wrap img {
    filter: saturate(.8) brightness(.55) sepia(.2);
    transform: scale(1.06);
  }

  /* Gradient over image */
  .pillar-img-wrap::after {
    content: '';
    position: absolute;
    inset: 0;
    background: linear-gradient(
      to bottom,
      transparent 0%,
      rgba(18,5,5,.7) 100%
    );
  }

  /* Icon badge over image */
  .pillar-icon-badge {
    position: absolute;
    bottom: -1.5rem;
    left: 50%;
    transform: translateX(-50%);
    width: 3.4rem;
    height: 3.4rem;
    border-radius: 50%;
    background: linear-gradient(135deg, var(--ciks-crimson2), var(--ciks-crimson));
    border: 2px solid var(--ciks-gold);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 2;
    box-shadow: 0 4px 20px rgba(0,0,0,.5);
    transition: transform .4s cubic-bezier(.22,1,.36,1), box-shadow .4s ease;
  }

  .pillar-card:hover .pillar-icon-badge {
    transform: translateX(-50%) scale(1.1);
    box-shadow: 0 6px 28px rgba(201,168,76,.3);
  }

  .pillar-icon-badge svg {
    width: 1.4rem;
    height: 1.4rem;
    color: var(--ciks-gold-lt);
  }

  /* Card body */
  .pillar-body {
    padding: 2.4rem 1.75rem 2rem;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    flex: 1;
  }

  /* Ornament divider */
  .pillar-ornament {
    display: flex;
    align-items: center;
    gap: .6rem;
    margin-bottom: 1.1rem;
  }

  .pillar-ornament::before,
  .pillar-ornament::after {
    content: '';
    width: 28px;
    height: 1px;
    background: var(--ciks-gold);
    opacity: .45;
  }

  .pillar-ornament span {
    font-size: .55rem;
    color: var(--ciks-gold);
    opacity: .7;
    letter-spacing: .1em;
  }

  /* Pillar title */
  .pillar-title {
    font-family: 'Cormorant Garamond', serif;
    font-size: 1.65rem;
    font-weight: 600;
    color: #fff;
    line-height: 1.2;
    margin-bottom: .85rem;
    transition: color .3s ease;
  }

  .pillar-card:hover .pillar-title {
    color: var(--ciks-gold-lt);
  }

  /* Pillar description */
  .pillar-desc {
    font-family: 'Lato', sans-serif;
    font-weight: 300;
    font-size: .92rem;
    color: rgba(255,255,255,.55);
    line-height: 1.75;
    transition: color .3s ease;
  }

  .pillar-card:hover .pillar-desc {
    color: rgba(255,255,255,.75);
  }

  /* Bottom tag */
  .pillar-tag {
    font-family: 'Cinzel', serif;
    font-size: .58rem;
    letter-spacing: .3em;
    text-transform: uppercase;
    color: var(--ciks-gold);
    opacity: 0;
    margin-top: 1.25rem;
    padding: .35rem .9rem;
    border: 1px solid rgba(201,168,76,.3);
    border-radius: 1px;
    transform: translateY(6px);
    transition: opacity .4s ease, transform .4s ease;
  }

  .pillar-card:hover .pillar-tag {
    opacity: 1;
    transform: translateY(0);
  }

  /* ── Connecting dots between cards (desktop only) ── */
  .connector {
    display: none;
  }
  @media (min-width: 900px) {
    .foundations-grid {
      position: relative;
    }
  }

  